.first{
    height: 100vh;
    padding: 115px 0;
    position: relative;
    min-height: 950px;
    max-height: 950px;
    &-container{
        height: 100%;
    }
    &__wrapper{
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        height: 100%;
        background-color: transparent;
        // background-image: url(./../../img/main-view-arrow.svg);
        // background-position: center right;
        // background-repeat: no-repeat;
        justify-content: center;
    }
    &__btn{
        width: fit-content;
        margin-top: 20px;
        color: var(--c-white);
        font-weight: 700;
    }
    .caption{
        display: flex;
        gap: 1.5rem;
        flex-direction: column;
    }
    .bg__img{
        position: absolute;
        right: 210px;
        width: 490px;
        height: 480px;
        svg{
            position: absolute;
            max-width: 100%;
            height: 100%;
            width: 100%;
            object-fit: cover;
            transition: 0.3s linear;
            polygon{
                transition: 0.3s linear;
                animation: samolet 3s linear infinite;
            }
        }
    }
}

@keyframes samolet {
    0%{
        filter: blur(0px);
    }
    50%{
        filter: blur(10px);
    }
    100%{
        filter: blur(0px);
    }
}